<template>
  <v-dialog fullscreen hide-overlay v-model="modal">
    <v-btn class="accent" dark flat slot="activator">Create People</v-btn>
    <v-card :class="localStoreColection.themeColor + '--text'">
      <v-container :style="{'zoom': localStoreColection.pageZoom, 
        'fontSize': localStoreColection.fontSize}">
        <v-layout row>
          <v-flex xs12>
            <v-card-title>
              <h1>Create a new User</h1>
            </v-card-title>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row elevation-10>
          <v-flex xs12>
            <v-card-text>
              <v-form ref="form" v-model.lazy="valid" validation>
                <h2 class="ml-4">Enter your name</h2>
                <v-text-field
                  prepend-icon="person"
                  name="name"
                  label="User Name *"
                  type="text"
                  :counter="4"
                  v-model="name"
                  :rules="nameRules"
                ></v-text-field>
                <h2 class="ml-4">Enter your email address</h2>
                <v-text-field 
                  prepend-icon="alternate_email" 
                  name="email" 
                  label="Email" 
                  type="email"
                  v-model="email"
                  :rules="emailRules"
                ></v-text-field>
                <v-layout>
                  <v-flex xs12>
                    <h2 class="ml-4 ma-3">Select Avatar</h2>
                    <ul class="list_avatar">
                      <li class="ma-3" v-for="avatar in avatars" :key="avatar.name">
                        <label class="radio">
                          <input 
                            type="radio" 
                            :name="avatar.name" 
                            class="color_cheked" 
                            v-model="userAvatar" 
                            :value="avatar.urlAvatar">
                            <div class="colorId" style="background: white"></div>
                          <v-img height="100" width="100" :src="avatar.urlAvatar"></v-img>
                        </label>
                      </li>
                    </ul>
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12>
            <v-card-actions class="mt-3">
              <v-spacer></v-spacer>
              <v-btn 
                class="error" 
                flat @click="onCancel"
                :disabled="localLoading"
                >Cancel</v-btn>
              <v-btn 
                class="success" 
                @click="onSave"
                :disabled="localLoading || !valid"
                :loading="localLoading"
              >
                Save
                <span slot="loader" class="custom-loader">
                  <v-icon light>cached</v-icon>
                </span>
              </v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: ['localStoreColection'],
    data () {
      return {
        // localStoreColection: {},
        modal: false,
        valid: false,
        localLoading: false,
        name: '',
        email: '',
        userAvatar: 'https://firebasestorage.googleapis.com/v0/b/vutify-jaerbi.appspot.com/o/avatar%2Favatar_unknown.png?alt=media&token=765161af-c240-44db-8afb-497887913bdb',
        emailRules: [
          v => !!v || 'Електронна пошта обов\'язкова',
          v => /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'Електронна пошта повинна бути дійсною'
        ],
        nameRules: [
          v => !!v || 'Name обов\'язковe',
          v => (v && v.length >= 4) || 'Name має містити не менше 6 символів'
        ]
      }
    },
    methods: {
      onCancel () {
        this.name = ''
        this.email = ''
        this.modal = false
        this.userAvatar = 'https://firebasestorage.googleapis.com/v0/b/vutify-jaerbi.appspot.com/o/avatar%2Favatar_unknown.png?alt=media&token=765161af-c240-44db-8afb-497887913bdb'
      },
      onSave () {
        console.log(this.userAvatar)
        if (this.name !== '' && this.email !== '') {
          this.localLoading = true
          this.$store.dispatch('createUser', {
            name: this.name,
            email: this.email,
            userAvatar: this.userAvatar,
            activUser: true
          })
          .finally(() => {
            this.name = ''
            this.email = ''
            this.userAvatar = 'https://firebasestorage.googleapis.com/v0/b/vutify-jaerbi.appspot.com/o/avatar%2Favatar_unknown.png?alt=media&token=765161af-c240-44db-8afb-497887913bdb'
            this.localLoading = false
            this.modal = false
          })
        }
      }
    },
    computed: {
      avatars () {
        return this.$store.getters.avatars
      }
    },
    // created () {
    //   this.localStoreColection = JSON.parse(localStorage.getItem('theme-option-page') || "{themeColor: 'primary',showBookm: true,fontSize: '1em',pageZoom: 1}")
    // }
  }
</script>

<style>
  .list_avatar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .list_avatar li {
    list-style: none;
  }
  .color_cheked {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 10px 0 0 7px;
  }
  .colorId {
    position: relative;
    cursor: pointer;
    border-radius: 50%;
    margin-left: 10px;
    width: 20px;
    height: 20px;
    box-shadow: 0px 0px 1px 1px rgba(0,0,0,.4);
  }
  .colorId:before {
    content: '';
    position: absolute;
    top:-1px;left:-1px;
    width: 22px;
    height: 22px;
    border: 1px solid transparent;
    border-radius: 50%;
  }
  .colorId:after {
    content: '';
    position: absolute;
    top:11px;left:11px;
    width: 10px;
    height: 10px;
    border: 3px solid transparent;
    border-radius:20%;
    border-bottom: 8px solid rgba(0,173,20,0.86);
    border-right: 8px solid rgba(0,173,20,0.86);
    opacity: 0;
    transition: .1s;
  }
  .radio input:checked + .colorId:after {
    opacity: 1;
  }
  .radio input:focus + .colorId:before {
    box-shadow: 1px 1px 4px 4px rgba(0,173,20,0.76);
  }
</style>




